body {
  font-size: 0.1875rem;
  background: #fff;
}
.Header {
  width: 100%;
  height: 1.171875rem;
}
.timeTxT {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.171875rem;
  z-index: 5;
  text-align: center;
  color: #fff;
  background: #0085ff;
}
.timeTxT .icon-direction_l {
  width: 0.78125rem;
  height: 0.78125rem;
  line-height: 0.78125rem;
  text-align: center;
  padding: 0.078125rem;
  position: absolute;
  top: 0.125rem;
  left: 0.15625rem;
  font-size: 0.46875rem;
  color: #fff;
}
.timeTxT div {
  height: 1.171875rem;
  line-height: 1.171875rem;
  font-size: 0.5625rem;
}
.question {
  padding: 0 0.46875rem 0.46875rem;
}
.question h3 {
  margin: 0.46875rem 0 0.234375rem 0;
  font-size: 0.40625rem;
  color: #333;
  font-weight: 600;
}
.question p {
  font-size: 0.34375rem;
  color: #666;
  margin: 0.15625rem 0;
}
.question ul {
  margin-left: 0.46875rem;
}
.question li {
  list-style: disc;
  font-size: 0.34375rem;
  color: #666;
  margin: 0.078125rem 0;
}



