body {
  font-size: 0.1875rem;
  background: #f5f5f5;
}
.Header {
  width: 100%;
  height: 1.171875rem;
}
.loginTxT {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.171875rem;
  z-index: 5;
  text-align: center;
  color: #fff;
  background: #0085ff;
}
.loginTxT .icon-direction_l {
  width: 0.78125rem;
  height: 0.78125rem;
  line-height: 0.78125rem;
  text-align: center;
  padding: 0.078125rem;
  position: absolute;
  top: 0.125rem;
  left: 0.15625rem;
  font-size: 0.46875rem;
}
.loginTxT div {
  height: 1.171875rem;
  line-height: 1.171875rem;
  font-size: 0.5625rem;
}
.userInfo {
  margin-top: 0.3125rem;
}
.infoItem {
  padding: 0.3125rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
  background: #fff;
  border-bottom: 0.015625rem solid #dddddd;
}
.borNone {
  border-bottom: none;
}
.infoItem .userList {
  font-size: 0.375rem;
  color: #666;
}
.infoItem .listTxt {
  font-size: 0.3125rem;
  color: #999;
  margin-right: 0.3125rem;
}
.infoItem .rightInfo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.infoItem img {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.3125rem;
  border-radius: 0.078125rem;
}
.infoItem .icon-direction_r {
  width: 0.5rem;
