  font-weight: normal;
  height: 0.78125rem;
  line-height: 0.78125rem;
}
.escLogin {
  width: 90%;
  height: 0.9375rem;
  line-height: 0.9375rem;
  border: 0;
  border-radius: 0.09375rem;
  background: #d8584a;
  color: #fff;
  text-align: center;
  font-size: 0.40625rem;
  outline: none;
  margin: 0.78125rem 5% 0 5%;
}
.mask {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: none;
}
.maskShow {
  display: block;
}
.maskCon {
  width: 70%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -80%);
  -ms-transform: translate(-50%, -80%);
  transform: translate(-50%, -80%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding-top: 0.46875rem;
  border-radius: 0.15625rem;
  background: #fff;
  -webkit-box-shadow: 0.015625rem 0.015625rem 0.09375rem #999999;
  box-shadow: 0.015625rem 0.015625rem 0.09375rem #999999;
}
.maskCon .circle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 1.25rem;
  height: 1.25rem;
  border: 0.078125rem solid #f8cb86;
  border-radius: 50%;
}
.maskCon .circle .circleS1 {
  width: 0.09375rem;
  height: 0.46875rem;
  background: #f8cb86;
}
.maskCon .circle .circleS2 {
  width: 0.09375rem;
  height: 0.09375rem;
  border-radius: 50%;
  background: #f8cb86;
  margin-top: 0.125rem;
}
.maskCon .tips {
  font-size: 0.40625rem;
  margin: 0.46875rem 0;
  color: #333;
}
.maskCon .maskDel {
  width: 100%;
  height: 1.25rem;
  background: #56d176;
  font-size: 0.40625rem;
  text-align: center;
  line-height: 1.25rem;
  color: #fff;
  font-weight: 700;
  border-radius: 0 0 0.15625rem 0.15625rem;
}



