body,
ul,
li,
dl,
dt,
dd,
input,
p,
h3,
button {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
a {
  display: block;
  text-decoration: none;
}
img {
  border: 0;
  display: block;
}
body {
  font-family: helvetica;
}
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
body {
  width: 100%;
  height: 100%;
  overflow: auto;
}
input {
  border: 0;
}
clearFix:after {
  content: ".";
  height: 0;
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
}
/*隐藏移动端滚动条*/
html::-webkit-scrollbar {
  display: none;
}
/*默认字体设置*/
body {
  font-family: Helvetica;
}
body * {
  /*禁止文字缩放*/
  -webkit-text-size-adjust: 100%;
  /*选中文字设置*/
  -webkit-user-select: none;
}
/*清除点击阴影*/
a,
input,
button {
  -webkit-tap-highlight-color: transparent;
}
/*消除圆角按钮（针对iOS系统）*/
input,
button {
  -webkit-appearance: none;
  border-radius: 0;
}
* {
  margin: 0;
  padding: 0;
}



