body {
  font-size: 0.1875rem;
  background: #f5f5f5;
}
.Header {
  width: 100%;
  height: 1.171875rem;
}
.loginTxT {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.171875rem;
  z-index: 5;
  text-align: center;
  color: #fff;
  background: #0085ff;
}
.loginTxT .icon-direction_l {
  width: 0.78125rem;
  height: 0.78125rem;
  line-height: 0.78125rem;
  text-align: center;
  padding: 0.078125rem;
  position: absolute;
  top: 0.125rem;
  left: 0.15625rem;
  font-size: 0.46875rem;
  color: #fff;
}
.loginTxT div {
  height: 1.171875rem;
  line-height: 1.171875rem;
  font-size: 0.5625rem;
}
.loginUser {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-top: 0.78125rem;
  width: 100%;
}
.loginUser img {
  width: 3.125rem;
  max-width: 100%;
  margin-bottom: 0.78125rem;
}
.loginItem {
  margin-bottom: 0.46875rem;
  width: 60%;
  position: relative;
}
.loginItem .inps {
  padding-left: 0.46875rem;
  width: 100%;
  height: 1.09375rem;
  line-height: 1.09375rem;
  color: #666;
  font-size: 0.375rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 0.09375rem;
  border: 0.015625rem solid #dddddd;
  outline: none;
}
.loginItem .pass {
  position: absolute;
  width: 1.25rem;
  height: 0.4375rem;
  border: 0.015625rem solid #dddddd;
  border-radius: 0.625rem;
  line-height: 0.4375rem;
  font-size: 0.25rem;
  overflow: hidden;
  top: 0.3125rem;
  right: 0.3125rem;
}
.loginItem .pass .bgLeft {
  background: #f5f5f5;
  height: 0.4375rem;
  padding: 0 0.15625rem;
  text-align: right;
  color: #999;
}
.loginItem .pass .bgRight {
  background: #00d762;
  height: 0.4375rem;
  padding: 0 0.15625rem;
  text-align: left;
  color: #fff;
}
.loginItem .pass .circleL {
  width: 0.4375rem;
  height: 0.4375rem;
  border: 0.015625rem solid #dddddd;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: -0.015625rem;
  left: -0.015625rem;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.loginItem .pass .circleR {
  width: 0.4375rem;
  height: 0.4375rem;
  border: 0.015625rem solid #dddddd;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: -0.015625rem;
  right: -0.015625rem;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.loginItem .inps:focus {
  border-color: #0089dc;
}
.loginItem .inps::-webkit-input-placeholder {
  color: #ddd;
}
.hint {
  width: 60%;
  margin-bottom: 0.46875rem;
}
.hint p {
  color: #999;
  font-size: 0.28125rem;
}
.hint a {
  color: #2395ff;
  font-size: 0.28125rem;
  display: inline-block;
}
.loginUser button {
  width: 60%;
  height: 0.9375rem;
  line-height: 0.9375rem;
  border: 0;
  border-radius: 0.09375rem;
  background: #56d176;
  color: #fff;
  text-align: center;
  font-size: 0.40625rem;
  outline: none;
}
.mask {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: none;
}
.maskShow {
  display: block;
}
.maskCon {
  width: 70%;
