  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 1.25rem;
  height: 1.25rem;
  border: 0.078125rem solid #f8cb86;
  border-radius: 50%;
}
.maskCon .circle .circleS1 {
  width: 0.09375rem;
  height: 0.46875rem;
  background: #f8cb86;
}
.maskCon .circle .circleS2 {
  width: 0.09375rem;
  height: 0.09375rem;
  border-radius: 50%;
  background: #f8cb86;
  margin-top: 0.125rem;
}
.maskCon .tips {
  font-size: 0.40625rem;
  margin: 0.46875rem 0;
  color: #333;
}
.maskCon .maskDel {
  width: 100%;
  height: 1.25rem;
  background: #56d176;
  font-size: 0.40625rem;
  text-align: center;
  line-height: 1.25rem;
  color: #fff;
  font-weight: 700;
  border-radius: 0 0 0.15625rem 0.15625rem;
}



