body {
  background: #fff;
}
.search {
  display: -ms-flexbox;
  display: flex;
  padding: 0.25rem;
  position: relative;
  z-index: 5;
  top: 0;
  left: 0;
}
.search .icon-direction_l {
  width: 0.78125rem;
  height: 0.78125rem;
  line-height: 0.78125rem;
  text-align: center;
  padding: 0.078125rem 0.15625rem 0.078125rem 0;
  font-size: 0.46875rem;
  color: #999;
}
.search input {
  -ms-flex-positive: 1;
  flex-grow: 1;
  background: #f8f8f8;
  border-radius: 0.46875rem;
  font-size: 0.375rem;
  padding: 0.25rem 0.375rem;
  outline: none;
}
.search input::-webkit-input-placeholder {
  color: #999;
}
.history {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 0.15625rem 0.3125rem;
  -ms-flex-align: center;
  align-items: center;
}
.history span {
  color: #666;
  font-size: 0.40625rem;
  font-weight: 700;
}
.history .icon-del {
  color: #999;
  font-size: 0.40625rem;
  padding: 0.078125rem;
}
.item_search {
  padding: 0 0.3125rem 0.3125rem 0.3125rem;
}
.item_search span {
  display: inline-block;
  border-radius: 0.15625rem;
  padding: 0.1875rem 0.265625rem;
  font-size: 0.375rem;
  margin: 0.3125rem 0.3125rem 0 0;
  color: #666;
  background: #f7f7f7;
}



