body {
  font-size: 0.1875rem;
  background: #fff;
}
.Header {
  width: 100%;
  height: 1.171875rem;
}
.serviceTxt {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.171875rem;
  z-index: 5;
  text-align: center;
  color: #fff;
  background: #0085ff;
}
.serviceTxt .icon-direction_l {
  width: 0.78125rem;
  height: 0.78125rem;
  line-height: 0.78125rem;
  text-align: center;
  padding: 0.078125rem;
  position: absolute;
  top: 0.125rem;
  left: 0.15625rem;
  font-size: 0.46875rem;
  color: #fff;
}
.serviceTxt div {
  height: 1.171875rem;
  line-height: 1.171875rem;
  font-size: 0.5625rem;
}
.Problem h3 {
  padding-left: 0.46875rem;
  line-height: 1.5625rem;
  height: 1.5625rem;
  font-size: 0.5rem;
  color: #333;
  font-weight: 400;
  border-bottom: 1px solid #ddd;
}
.Problem ul li {
  border-bottom: 1px solid #ddd;
}
.Problem ul li .item_a {
  padding: 0.40625rem 0.46875rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.Problem ul li .item_a span {
  font-size: 0.34375rem;
  color: #666;
}
.Problem ul li .item_a .icon-direction_r {
  width: 0.5rem;
  height: 0.5rem;
  text-align: center;
  line-height: 0.5rem;
  font-size: 0.34375rem;
  color: #666;
}



